export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params,
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err);
      });
  });
}

export function post(params, type) {
  return new Promise((resolve, reject) => {
    axios
      .post('', params)
      .then((res) => {
        resolve({
          data: {
            results:
              type == 'market'
                ? res.data.data.marketItemRecEntities
                : res.data.data.profitEntities,
          },
        });
      })
      .catch((err) => {
        reject(err);
      });
  });
}
