import { post, get } from './index';

// false-第三方
// true - 自己服务
const myInterface = true;

// //市场出售单
export const getMarketSell = (page, type) => {
  const pageNumber = page * 20;
  return myInterface
    ? get('market', {
        page,
        count: 20,
        owner: '0x0000000000000000000000000000000000000000',
        orderby: type == 'selltime' ? 0 : 1,
      })
    : post(
        {
          query: `query MyQuery {\n  marketItemRecEntities (first: 20, skip:${pageNumber}  where:{owner: "0x0000000000000000000000000000000000000000"} orderBy:${type} orderDirection:asc ) {\n    id\n    index\n    owner\n    price\n    seller\n    tokenId\n    selltime\n  }\n}`,
          variables: null,
          operationName: 'MyQuery',
        },
        'market'
      );
};

// //像素格的交易记录
export const getPixelRecord = (tokenId, page) => {
  const pageNumber = page * 20;
  return myInterface
    ? get('market', {
        page,
        tokenId,
        count: 20,
        owner_not: '0x0000000000000000000000000000000000000000',
        orderby: 0,
      })
    : post(
        {
          query: `query MyQuery {\n  marketItemRecEntities (first: 20, skip:${pageNumber}  where:{tokenId:${tokenId} owner_not:"0x0000000000000000000000000000000000000000" }  orderBy:selltime orderDirection:desc) {\n    id\n    index\n    owner\n    price\n    seller\n    tokenId\n    selltime\n  }\n}`,
          variables: null,
          operationName: 'MyQuery',
        },
        'market'
      );
};

//自己的出售中的像素
export const getUserSell = (address, page) => {
  const pageNumber = page * 20;
  return myInterface
    ? get('market', {
        page,
        count: 20,
        seller: address,
        owner: '0x0000000000000000000000000000000000000000',
        orderby: 0,
      })
    : post(
        {
          query: `query MyQuery {\n  marketItemRecEntities (first: 20, skip:${pageNumber} where:{owner:"0x0000000000000000000000000000000000000000" seller:"${address}" } orderBy:id orderDirection:desc) {\n    id\n    index\n    owner\n    price\n    seller\n    tokenId\n    selltime\n  }\n}`,
          variables: null,
          operationName: 'MyQuery',
        },
        'market'
      );
};

//收益记录
export const getProfitList = (address, page) => {
  const pageNumber = page * 20;
  return myInterface
    ? get('profitrec', {
        page,
        count: 20,
        user: address,
      })
    : post(
        {
          query: `query MyQuery { profitEntities(first: 20, skip:${pageNumber} where:{user:"${address}"} orderBy:time orderDirection:desc) {
  id
  user
  type_
  value
  time
} }`,
        },
        'account'
      );
};

